@import 'assets/style/function.scss';

.headerBox {
  width: 100%;
  background-color: black;
  display: flex;
  height: 58px;
  color: #ffffff;
  @include media-ta {
    justify-content: space-between;
  }
  ul {
    vertical-align: middle;
    @include media-ta {
      padding: 0 5px;
    }
    li {
      @include media-ta {
        padding: 17px 5px;
      }
      p {
        vertical-align: middle;
        display: inline-block;
        margin: 0;
        font-size: 14px;
        @include media-ta {
          padding: 0 5px;
        }
      }
    }
  }
}
nav {
  li {
    vertical-align: middle;
    &:hover {
      background-color: aqua;
    }
    i {
      color: #ffffff;
    }
  }
}

.header {
  text-align: center;
  h1 {
    margin-top: 50px;
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 30px;
    @include font('cc');
  }
  a {
    &.is-active {
      font-weight: bold;
    }
  }
}

@media(max-width:676px) {
  nav {
    li {
      &:nth-child(1) {
        display: none;
      }
      &:nth-child(2) {
        display: none;
      }
      &:nth-child(3) {
        display: none;
      }
    }
  }
}

@media(max-width:1023px) {
  nav {
    ul.right {
      display: flex;
      li {
        padding: 15px 10px;
        &:nth-child(1) {
          display: none;
        }
        &:nth-child(2) {
          display: none;
        }
        &:nth-child(3) {
          display: none;
        }
      }
    }
  }
}


@media(min-width:1024px) {
  .headerBox {
    justify-content: center;
    ul {
      padding: 0 500px 0 0px;
      &:after {
        display: block;
        clear: both;
        content:'';
      }
      li {
        padding: 17px 20px;
        float: left;
        p {
          padding: 0 5px;
        }
      }
    }
  }

  Nav {
    display: flex;
    align-items: flex-end;
    //FIXME ul에 class안 붙히면 오른쪽 정렬이 안됨
    ul.right {
      padding: 0 0 0 100px;
      li {
        padding: 15px 20px;
      }
    }
  }
}